.contact_container {
  h3 {
    font-family: 'Google Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    margin-bottom: 10px;
  }

  p {
    margin-bottom: 10px;
    font-family: 'Google Sans';
    font-style: normal;
    font-size: 17px;
  }

  li {
    margin-bottom: 2px;
    font-family: 'Google Sans';
    font-style: normal;
    font-size: 17px;
  }

  .contact_content {
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media screen and (max-width: 600px) {
    a {
      word-wrap: break-word;
    }
  }
}
